.footer {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 40px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.link-list {
  list-style: none;
  height: 100%;
}

.link-list:after {
  display: block;
  clear:both;
  height:0;
  content: "";
  visibility: hidden;
  overflow:hidden;
}

.link-list li {
  width: 60px;
  height: 100%;
  float: left;
}

.link-list button {
  display: block;
  width: 60px;
  height: 100%;
  line-height: 100%;
}


